	export default {
		onLoad: function(e) {
			console.log(e.SplitNo)
			this.LabelEntity.SplitNo = e.SplitNo
			if (this.LabelEntity.SplitNo != '' && this.LabelEntity.SplitNo != undefined) {
				uni.setNavigationBarTitle({
					title: '拆盘上架:' + e.SplitNo
				})

			}
		},
		data() {
			return {
				LabelEntity: {
					Code: '',
					SupplyCode: '',
					MaterialCode: '',
					ProductionDate: '',
					Batchcode: '',
					Quantity: 0,
					LocationCode: '',
					SplitNo: '',
					ShelfDetailId: 0,
					ReplenishCode: ''
				},
				labelFoucs: false,
				ScanMaterialLabel: '',
				IsFirst: true,
				IsLastFinished: false,
				LastDetailId: 0,
				locationFoucs: false,
				IsCanSkipLocation: false,
				IsCanStartOrder: true
			}
		},
		methods: {
			/* 			ScanCode(){
							uni.scanCode({
								scanType:'barCode',
								 success:function(res){
								 	console.log(res.result)
									this.LabelEntity.Code=res.result
								 }
							})
						}, */
			OnEnter(e) {
				uni.showModal({
					content: this.LabelEntity.SplitNo,
					showCancel: false
				});
				console.log(this.IsFirst)
				this.LabelEntity.Code = this.ScanMaterialLabel
				this.ScanMaterialLabel = ''
				this.textFoucs = true
				if (this.IsFirst == false) {
					this.ScanKeyDown()
				}
			},
			ScanKeyDown() {
				if (this.IsFirst) {
					var ReelId = this.LabelEntity.Code
					if (this.LabelEntity.Code == '') {
						uni.showModal({
							content: '未扫描条码!',
							showCancel: true
						});
						this.labelFoucs = true
						return
					}
					if (this.LabelEntity.LocationCode == '') {
						uni.showModal({
							content: '未扫描库位码!',
							showCancel: true
						});
						this.locationFoucs = true
						return
					}
					this.api.baseRequest({
						url: 'Shelf/FirstShelf',
						method: "post",
						data: this.LabelEntity,
						success: res => {
							var resData = JSON.parse(res.data.Content)
							if (resData.Success) {
								console.log(resData)
								this.LabelEntity = resData.Data
								this.labelFoucs = true
								this.IsFirst = false
								this.IsLastFinished = false
								this.IsCanStartOrder = false
								this.IsCanSkipLocation = true
								console.log(this.LabelEntity)
							} else {
								uni.showModal({
									content: resData.Message,
									showCancel: false
								});
								return
							}

						},
						fail: () => {

						},
						complete: () => {

						}
					})

				} else {
					if (this.LabelEntity.Code == '') {
						uni.showModal({
							content: '未扫描条码!',
							showCancel: true
						});
						this.labelFoucs = true
						return
					}
					if (this.LabelEntity.ShelfDetailId != 0 && this.IsLastFinished == false) {
						this.api.baseRequest({
							url: 'Shelf/PdaConfirmShelf',
							method: "post",
							data: this.LabelEntity,
							success: res => {
								var resData = JSON.parse(res.data.Content)
								if (resData.Success) {
									this.LabelEntity = resData.Data
									this.labelFoucs = true
									this.IsFirst = false
									this.IsLastFinished = true
                                    this.api.baseRequest({
                                    	url: 'Shelf/NextShelf',
                                    	method: "post",
                                    	data: this.LabelEntity,
                                    	success: res => {
                                    		var resData = JSON.parse(res.data.Content)
                                    		if (resData.Success) {
                                    			this.LabelEntity = resData.Data
                                    			this.labelFoucs = true
                                    			this.IsFirst = false
                                    			this.IsLastFinished = false
                                    		} else {
                                    			uni.showModal({
                                    				content: resData.Message,
                                    				showCancel: false
                                    			});
                                    			return
                                    		}
                                    
                                    	},
                                    	fail: () => {
                                    
                                    	},
                                    	complete: () => {
                                    
                                    	}
                                    })
                                    							
								} else {
									uni.showModal({
										content: resData.Message,
										showCancel: false
									});
									this.labelFoucs = true
									return
								}

							},
							fail: () => {

							},
							complete: () => {
					
							}
						})
					} else {
						this.api.baseRequest({
							url: 'Shelf/NextShelf',
							method: "post",
							data: this.LabelEntity,
							success: res => {
								var resData = JSON.parse(res.data.Content)
								if (resData.Success) {
									this.LabelEntity = resData.Data
									this.labelFoucs = true
									this.IsFirst = false
									this.IsLastFinished = false
								} else {
									uni.showModal({
										content: resData.Message,
										showCancel: false
									});
									return
								}

							},
							fail: () => {

							},
							complete: () => {

							}
						})

					}

				}

			},
			ConfirmShelf() {
				if (this.LabelEntity.ShelfDetailId != 0 && this.IsLastFinished == false) {
					this.api.baseRequest({
						url: 'Shelf/PdaConfirmShelf',
						method: "post",
						data: this.LabelEntity,
						success: res => {
							var resData = JSON.parse(res.data.Content)
							if (resData.Success) {
								this.LabelEntity = resData.Data
								this.labelFoucs = true
								this.IsFirst = false
								this.IsLastFinished = true

							} else {
								uni.showModal({
									content: resData.Message,
									showCancel: false
								});
								this.labelFoucs = true
								return
							}

						},
						fail: () => {

						},
						complete: () => {

						}
					})

				} else {
					uni.showModal({
						content: '尚未获取到上架信息!',
						showCancel: true
					});
					this.labelFoucs = true
				}
			},
			FinisheCurrentOrder() {
				if (this.IsLastFinished == false && this.LabelEntity.ShelfDetailId != 0) {
					uni.showModal({
						title: '提示',
						content: '是否确定完成此次上架任务',
						success: function(res) {
							if (res.confirm) {
								this.api.baseRequest({
									url: 'Shelf/FinishCurrentReplenish',
									method: "post",
									data: this.LabelEntity,
									success: res => {
										var resData = JSON.parse(res.data.Content)
										if (resData.Success) {
											this.LabelEntity = resData.Data
											this.LabelEntity.LocationCode=''
											this.labelFoucs = true
											this.IsFirst = true
											this.IsLastFinished = false
											this.IsCanSkipLocation = false
											this.IsCanStartOrder = true
										} else {
											uni.showModal({
												content: resData.Message,
												showCancel: false
											});
											return
										}

									},
									fail: () => {

									},
									complete: () => {

									}
								})
							} else if (res.cancel) {

							}
						}.bind(this)
					})

				} else {
					this.api.baseRequest({
						url: 'Shelf/FinishCurrentReplenish',
						method: "post",
						data: this.LabelEntity,
						success: res => {
							var resData = JSON.parse(res.data.Content)
							if (resData.Success) {
								this.LabelEntity = resData.Data
								this.labelFoucs = true
								this.IsFirst = true
								this.IsLastFinished = false
								this.IsCanSkipLocation = false
								this.IsCanStartOrder = true
							} else {
								uni.showModal({
									content: resData.Message,
									showCancel: false
								});
								return
							}

						},
						fail: () => {

						},
						complete: () => {

						}
					})
				}
			},

			SkipCurrentLocation() {
				if (this.IsFirst == false && this.LabelEntity.ShelfDetailId != 0 && this.IsLastFinished == false) {
					this.api.baseRequest({
						url: 'Shelf/SkipCurrentLightLocation',
						method: "post",
						data: this.LabelEntity,
						success: res => {
							var resData = JSON.parse(res.data.Content)
							if (resData.Success) {
								this.LabelEntity = resData.Data
								this.labelFoucs = true
								this.IsFirst = false
								this.IsLastFinished = false

							} else {
								uni.showModal({
									content: resData.Message,
									showCancel: false
								});
								this.labelFoucs = true
								return
							}

						},
						fail: () => {

						},
						complete: () => {

						}
					})

				}
			}
		}
	}
